import SimpleAddress from '../../../components/simple-address/simple-address.vue'
import HButton from '../../../components/HButton.vue'
import { updateUserInfo} from '../../../api/index.js'

export default {
	components: {
		SimpleAddress,
		HButton
	},
	data(){
		return{
			form:{
				name:undefined,
				city:undefined,
				complex:undefined,
				numberPlate:undefined
			},
			rules:{
				sno: [
				    { required: true, message: '请输入活动名称', trigger: 'blur' }
				]
			},
			region:undefined,
			cityPickerValueDefault:[0,0,0]
			
		}
	},
	methods:{
		snoInput(e){
			this.form.sno = e.detail.value
		},
		nameInput(e){
			this.form.name = e.detail.value
		},
		complexInput(e){
			this.form.complex = e.detail.value
		},
		numberPlateInput(e){
			this.form.numberPlate = e.detail.value
		},
		openAddres() {
			if(this.region){
				var index = this.$refs.simpleAddress.queryIndex(this.region, 'label');
				// console.log(index);
				this.cityPickerValueDefault = index.index;
			}else{
				this.cityPickerValueDefault = [0,0,0]
			}
			
			this.$refs.simpleAddress.open();
		},
		onConfirm(e){
			// console.log('confirm',e)
			this.region = e.labelArr
			this.form.city = this.region.join('')
			this.cityPickerValueDefault = e.value
			// this.region = JSON.stringify(e);
		},
		submit(){
			for (let key in this.form) {
			  if (!this.form[key] || this.form[key] == '' ) {
				wx.showToast({
				  title: '请将信息填写完整',
				  icon: 'none'
				})
				return
			  }
			}
			console.log('提交')
			let userId = wx.getStorageSync('userId')
			
			let houseNum = {
			  userId:userId,
			  name:this.form.name,
			  address:this.form.city + this.form.complex + this.form.numberPlate
			}
						
			let data = {
			  id:userId,
			  houseNum:JSON.stringify(houseNum)
			}
			updateUserInfo(data).then(res=>{
				if(res.code==1){
					uni.showToast({
						title:'提交成功',
						icon:'success'
					})
					setTimeout(()=>{
						wx.switchTab({
							url: '/pages/Tabbar/mine/index',
						  })
					},1000)
				}else{
					uni.showToast({
						title:rej.message,
						icon:'none'
					})
				}
			})
		}
	}
}
